import { Component, EventEmitter, Prop, Event, h, State } from '@stencil/core';
import * as _v from '../../utils/formValidators';
import { sortedCountriesList, getCountryPropByKey } from '../../utils/countriesList';
import flags from '../../utils/countriesFlags';
import { iFormField } from '../../models/form-field.model';

@Component({
  tag: 'phone-number-field',
  styleUrl: 'phone-number-field.css',
  shadow: false,
  scoped: false,
})
export class PhoneNumberField {
  @Prop() field: iFormField;
  @Prop() language: string;
  @Prop() isInvalid: boolean;
  @Prop() isHidden: boolean;
  @Prop() isDisabled: boolean;

  @State() values = { countryCode: '', phoneNumber: '' };
  @State() isTouched: boolean = false;

  @Event({ bubbles: true, composed: true }) valueChanged: EventEmitter<{ key: string; value: any }>;
  @Event({ bubbles: true, composed: true }) fieldBlurred: EventEmitter<{ key: string; }>;

  validationErrors: string[] = [];


  connectedCallback() {
    const [countryCode, phoneNumber] = (this.field.value || '').split('-');
    this.values = { countryCode: countryCode || this.field.defaultCountryCode || '+962', phoneNumber: phoneNumber || '' };
    this.valueChanged.emit({ key: this.field.key, value: `${this.values.countryCode}-${this.values.phoneNumber}` });
  }

  private handleBlur() {
    this.isTouched = true;
    this.fieldBlurred.emit({ key: this.field.key });
  }

  updateValue(property: 'countryCode' | 'phoneNumber', e: Event) {
    const value = (e.target as HTMLInputElement).value;
    const { ...values } = this.values;
    values[property] = value;

    this.values = values;
    const number = values.phoneNumber.startsWith('0')
      ? values.phoneNumber.slice(1)
      : values.phoneNumber;
    this.valueChanged.emit({ key: this.field.key, value: `${this.values.countryCode}-${number}` });
  }

  render() {
    if (this.isHidden) {
      return null;
    }

    const alpha_3_code = getCountryPropByKey('country_code', this.values.countryCode, `alpha_3_code`) || 'N/A';

    return (
      <section class={`phone-number-field-section ${this.language}`}>

        <div class={`${this.isInvalid ? 'invalid' : 'valid'} ${this.isTouched ? 'touched' : 'untouched'}`}>
          <select
            id={`${this.field.key}-countryCode`}
            onChange={(e) => this.updateValue('countryCode', e)}
            disabled={this.isDisabled}>
            <option value="" selected={!this.values.countryCode}>Select an option</option>

            {sortedCountriesList(this.language)?.map(option => {
              const optionValue = option['country_code'];
              const optionLabel = option[`short_name_${this.language}`];
              const optionFlag = flags[option['alpha_3_code']];
              return (
                <option value={optionValue} selected={optionValue === this.values.countryCode}>
                  {optionLabel} {optionFlag}
                </option>
              );
            })}
          </select>

          <label
            htmlFor={`${this.field.key}-countryCode`}
            dir='ltr'>
            <span style={{ fontSize: '24px' }}>{flags[alpha_3_code]}</span>
            &nbsp;{alpha_3_code}
          </label>

          <input
            dir="ltr"
            id={this.field.key}
            type='tel'
            autoComplete="off"
            disabled={this.isDisabled}
            placeholder={'Phone Number'}
            onInput={(e) => this.updateValue('phoneNumber', e)}
            onBlur={this.handleBlur.bind(this)}
          />
        </div>

        {this.field.label && (
          <label
            htmlFor={this.field.key}
            innerHTML={this.field.label.replace(/\[\--(.*?)\--\]/g, ' <span class="file-size-limit">($1)</span>')}>
          </label>
        )}

        <slot></slot>
      </section>
    );
  }
}
